nav {
    background-color: lightgray;
    overflow: hidden;
}
  
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
  
nav ul li {
  float: left;
}
  
nav ul li a {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
  
nav ul li a:hover {
  background-color: #ddd;
  color: black;
}